import React from "react";
import * as S from "./LoginDemo.styles";
import {
    ILoginDemo,
    LoginDemoItemDescProps,
    LoginDemoItemImageProps,
    LoginDemoItemProps,
    LoginDemoItemTitleProps
} from "./LoginDemo.types";
import TinySlider from "../../TinySlider";

const LoginDemo: ILoginDemo = ({children}) => {
    return (
        <S.LoginDemo className="LoginDemo">
            <S.LoginDemoInner className="LoginDemo__Inner">
                <TinySlider>
                    {children}
                </TinySlider>
            </S.LoginDemoInner>
        </S.LoginDemo>
    )
}


const LoginDemoItem: React.FC<LoginDemoItemProps> = ({className, children, background}) => {
    return (
        <S.LoginDemoItem background={background}>
            {children}
        </S.LoginDemoItem>
    )
}

const LoginDemoItemTitle: React.FC<LoginDemoItemTitleProps> = ({className, children}) => {

  return (
        <S.LoginDemoItemTitle>
            {children}
        </S.LoginDemoItemTitle>
    )
}

const LoginDemoItemDesc: React.FC<LoginDemoItemDescProps> = ({className, children}) => {
    return (
        <S.LoginDemoItemDesc>
            {children}
        </S.LoginDemoItemDesc>
    )
}

const LoginDemoItemImage: React.FC<LoginDemoItemImageProps> = ({className, children}) => {
    return (
        <S.LoginDemoItemImage>
            {children}
        </S.LoginDemoItemImage>
    )
}

LoginDemo.Item = LoginDemoItem;
LoginDemo.ItemTitle = LoginDemoItemTitle;
LoginDemo.ItemDesc = LoginDemoItemDesc;
LoginDemo.ItemImage = LoginDemoItemImage;

export default LoginDemo;